'use strict';

var Dicer = require('dicer');

var Content = require('content');

var stream = require('stream');

var util = require('util');

var Transform = stream.Transform;
var multipartFormdataType = 'multipart/form-data';
var applicationDirectory = 'application/x-directory';
var applicationSymlink = 'application/symlink';

var isDirectory = function isDirectory(mediatype) {
  return mediatype === multipartFormdataType || mediatype === applicationDirectory;
};

var parseDisposition = function parseDisposition(disposition) {
  var details = {};
  details.type = disposition.split(';')[0];

  if (details.type === 'file' || details.type === 'form-data') {
    var namePattern = /\ filename=\"(.[^\"]+)\"/;
    var matches = disposition.match(namePattern);
    details.name = matches ? matches[1] : '';
  }

  return details;
};

var parseHeader = function parseHeader(header) {
  var type = Content.type(header['content-type'][0]);
  var disposition = parseDisposition(header['content-disposition'][0]);
  var details = type;
  details.name = disposition.name;
  details.type = disposition.type;
  return details;
};

function Parser(options) {
  var _this = this;

  // allow use without new
  if (!(this instanceof Parser)) {
    return new Parser(options);
  }

  this.dicer = new Dicer({
    boundary: options.boundary
  });
  this.dicer.on('part', function (part) {
    return _this.handlePart(part);
  });
  this.dicer.on('error', function (err) {
    return _this.emit('err', err);
  });
  this.dicer.on('finish', function () {
    _this.emit('finish');

    _this.emit('end');
  });
  Transform.call(this, options);
}

util.inherits(Parser, Transform);

Parser.prototype._transform = function (chunk, enc, cb) {
  this.dicer.write(chunk, enc);
  cb();
};

Parser.prototype._flush = function (cb) {
  this.dicer.end();
  cb();
};

Parser.prototype.handlePart = function (part) {
  var _this2 = this;

  part.on('header', function (header) {
    var partHeader = parseHeader(header);

    if (isDirectory(partHeader.mime)) {
      part.on('data', function () {
        return false;
      });

      _this2.emit('directory', partHeader.name);

      return;
    }

    if (partHeader.mime === applicationSymlink) {
      part.on('data', function (target) {
        return _this2.emit('symlink', partHeader.name, target.toString());
      });
      return;
    }

    if (partHeader.boundary) {
      // recursively parse nested multiparts
      var parser = new Parser({
        boundary: partHeader.boundary
      });
      parser.on('file', function (file) {
        return _this2.emit('file', file);
      });
      part.pipe(parser);
      return;
    }

    _this2.emit('file', partHeader.name, part);
  });
};

module.exports = Parser;