'use strict';

var assert = require('assert');

var createBuildStream = require('./create-build-stream');

var Builder = require('./builder');

var reducers = {
  flat: require('./flat'),
  balanced: require('./balanced'),
  trickle: require('./trickle')
};
var defaultOptions = {
  strategy: 'balanced',
  highWaterMark: 100,
  reduceSingleLeafToSelf: true
};

module.exports = function (Chunker, ipld, _options) {
  assert(Chunker, 'Missing chunker creator function');
  assert(ipld, 'Missing IPLD');
  var options = Object.assign({}, defaultOptions, _options);
  var strategyName = options.strategy;
  var reducer = reducers[strategyName];
  assert(reducer, 'Unknown importer build strategy name: ' + strategyName);
  var createStrategy = Builder(Chunker, ipld, reducer, options);
  return createBuildStream(createStrategy, ipld, options);
};