'use strict';

var extend = require('deep-extend');

var UnixFS = require('ipfs-unixfs');

var pull = require('pull-stream/pull');

var values = require('pull-stream/sources/values');

var collect = require('pull-stream/sinks/collect');

var through = require('pull-stream/throughs/through');

var pullThrough = require('pull-through');

var parallel = require('async/parallel');

var waterfall = require('async/waterfall');

var paraMap = require('pull-paramap');

var persist = require('../utils/persist');

var reduce = require('./reduce');

var _require = require('ipld-dag-pb'),
    DAGNode = _require.DAGNode;

var defaultOptions = {
  chunkerOptions: {
    maxChunkSize: 262144,
    avgChunkSize: 262144
  },
  rawLeaves: false,
  hashAlg: 'sha2-256',
  leafType: 'file',
  cidVersion: 0,
  progress: function progress() {}
};

module.exports = function builder(createChunker, ipld, createReducer, _options) {
  var options = extend({}, defaultOptions, _options);
  options.progress = typeof options.progress === 'function' ? options.progress : defaultOptions.progress;
  return function (source) {
    return function (items, cb) {
      parallel(items.map(function (item) {
        return function (cb) {
          if (!item.content) {
            // item is a directory
            return createAndStoreDir(item, function (err, node) {
              if (err) {
                return cb(err);
              }

              if (node) {
                source.push(node);
              }

              cb();
            });
          } // item is a file


          createAndStoreFile(item, function (err, node) {
            if (err) {
              return cb(err);
            }

            if (node) {
              source.push(node);
            }

            cb();
          });
        };
      }), cb);
    };
  };

  function createAndStoreDir(item, callback) {
    // 1. create the empty dir dag node
    // 2. write it to the dag store
    var d = new UnixFS('directory');
    waterfall([function (cb) {
      return DAGNode.create(d.marshal(), [], cb);
    }, function (node, cb) {
      return persist(node, ipld, options, cb);
    }], function (err, result) {
      if (err) {
        return callback(err);
      }

      callback(null, {
        path: item.path,
        multihash: result.cid.buffer,
        size: result.node.size
      });
    });
  }

  function createAndStoreFile(file, callback) {
    if (Buffer.isBuffer(file.content)) {
      file.content = values([file.content]);
    }

    if (typeof file.content !== 'function') {
      return callback(new Error('invalid content'));
    }

    var reducer = createReducer(reduce(file, ipld, options), options);
    var chunker;

    try {
      chunker = createChunker(options.chunkerOptions);
    } catch (error) {
      return callback(error);
    }

    var previous;
    var count = 0;
    pull(file.content, chunker, through(function (buffer) {
      options.progress(buffer.length);
    }), paraMap(function (buffer, callback) {
      waterfall([function (cb) {
        if (options.rawLeaves) {
          return cb(null, {
            size: buffer.length,
            leafSize: buffer.length,
            data: buffer
          });
        }

        var file = new UnixFS(options.leafType, buffer);
        DAGNode.create(file.marshal(), [], function (err, node) {
          if (err) {
            return cb(err);
          }

          cb(null, {
            size: node.size,
            leafSize: file.fileSize(),
            data: node
          });
        });
      }, function (leaf, cb) {
        persist(leaf.data, ipld, options, function (error, results) {
          if (error) {
            return cb(error);
          }

          cb(null, {
            size: leaf.size,
            leafSize: leaf.leafSize,
            data: results.node,
            multihash: results.cid.buffer,
            path: leaf.path,
            name: ''
          });
        });
      }], callback);
    }), pullThrough( // mark as single node if only one single node
    function onData(data) {
      count++;

      if (previous) {
        this.queue(previous);
      }

      previous = data;
    }, function ended() {
      if (previous) {
        if (count === 1) {
          previous.single = true;
        }

        this.queue(previous);
      }

      this.queue(null);
    }), reducer, collect(function (err, roots) {
      if (err) {
        callback(err);
      } else {
        callback(null, roots[0]);
      }
    }));
  }
};