'use strict';

var BufferList = require('bl');

var through = require('pull-through');

module.exports = function (options) {
  var maxSize = typeof options === 'number' ? options : options.maxChunkSize;
  var bl = new BufferList();
  var currentLength = 0;
  var emitted = false;
  return through(function onData(buffer) {
    bl.append(buffer);
    currentLength += buffer.length;

    while (currentLength >= maxSize) {
      this.queue(bl.slice(0, maxSize));
      emitted = true; // throw away consumed bytes

      if (maxSize === bl.length) {
        bl = new BufferList();
        currentLength = 0;
      } else {
        var newBl = new BufferList();
        newBl.append(bl.shallowSlice(maxSize));
        bl = newBl; // update our offset

        currentLength -= maxSize;
      }
    }
  }, function onEnd() {
    if (currentLength) {
      this.queue(bl.slice(0, currentLength));
      emitted = true;
    }

    if (!emitted) {
      this.queue(Buffer.alloc(0));
    }

    this.queue(null);
  });
};