'use strict';

var eachSeries = require('async/eachSeries');

var eachOfSeries = require('async/eachOfSeries');

var waterfall = require('async/waterfall');

var createQueue = require('async/queue');

var writable = require('pull-write');

var pushable = require('pull-pushable');

var DirFlat = require('./dir-flat');

var flatToShard = require('./flat-to-shard');

var Dir = require('./dir');

var toPathComponents = require('../utils/to-path-components');

module.exports = createTreeBuilder;
var defaultOptions = {
  wrap: false,
  shardSplitThreshold: 1000,
  onlyHash: false
};

function createTreeBuilder(ipld, _options) {
  var options = Object.assign({}, defaultOptions, _options);
  var queue = createQueue(consumeQueue, 1); // returned stream

  var stream = createStream(); // root node

  var tree = DirFlat({
    path: '',
    root: true,
    dir: true,
    dirty: false,
    flat: true
  }, options);
  return {
    flush: flushRoot,
    stream: getStream
  };

  function consumeQueue(action, callback) {
    var args = action.args.concat(function () {
      action.cb.apply(null, arguments);
      callback();
    });
    action.fn.apply(null, args);
  }

  function getStream() {
    return stream;
  }

  function createStream() {
    var sink = writable(write, null, 1, ended);
    var source = pushable();
    return {
      sink: sink,
      source: source
    };

    function write(elems, callback) {
      eachSeries(elems, function (elem, callback) {
        queue.push({
          fn: addToTree,
          args: [elem],
          cb: function cb(err) {
            if (err) {
              callback(err);
            } else {
              source.push(elem);
              callback();
            }
          }
        });
      }, callback);
    }

    function ended(err) {
      flushRoot(function (flushErr) {
        source.end(flushErr || err);
      });
    }
  } // ---- Add to tree


  function addToTree(elem, callback) {
    var pathElems = toPathComponents(elem.path || '');
    var parent = tree;
    var lastIndex = pathElems.length - 1;
    var currentPath = '';
    eachOfSeries(pathElems, function (pathElem, index, callback) {
      if (currentPath) {
        currentPath += '/';
      }

      currentPath += pathElem;
      var last = index === lastIndex;
      parent.dirty = true;
      parent.multihash = null;
      parent.size = null;

      if (last) {
        waterfall([function (callback) {
          return parent.put(pathElem, elem, callback);
        }, function (callback) {
          return flatToShard(null, parent, options.shardSplitThreshold, options, callback);
        }, function (newRoot, callback) {
          tree = newRoot;
          callback();
        }], callback);
      } else {
        parent.get(pathElem, function (err, treeNode) {
          if (err) {
            callback(err);
            return; // early
          }

          var dir = treeNode;

          if (!dir || !(dir instanceof Dir)) {
            dir = DirFlat({
              dir: true,
              parent: parent,
              parentKey: pathElem,
              path: currentPath,
              dirty: true,
              flat: true
            }, options);
          }

          var parentDir = parent;
          parent = dir;
          parentDir.put(pathElem, dir, callback);
        });
      }
    }, callback);
  } // ---- Flush


  function flushRoot(callback) {
    queue.push({
      fn: flush,
      args: ['', tree],
      cb: function cb(err, node) {
        if (err) {
          callback(err);
        } else {
          callback(null, node && node.multihash);
        }
      }
    });
  }

  function flush(path, tree, callback) {
    if (tree.dir) {
      if (tree.root && tree.childCount() > 1 && !options.wrap) {
        callback(new Error('detected more than one root'));
        return; // early
      }

      tree.eachChildSeries(function (key, child, callback) {
        flush(path ? path + '/' + key : key, child, callback);
      }, function (err) {
        if (err) {
          callback(err);
          return; // early
        }

        flushDir(path, tree, callback);
      });
    } else {
      // leaf node, nothing to do here
      process.nextTick(callback);
    }
  }

  function flushDir(path, tree, callback) {
    // don't create a wrapping node unless the user explicitely said so
    if (tree.root && !options.wrap) {
      tree.onlyChild(function (err, onlyChild) {
        if (err) {
          callback(err);
          return; // early
        }

        callback(null, onlyChild);
      });
      return; // early
    }

    if (!tree.dirty) {
      callback(null, tree.multihash);
      return; // early
    } // don't flush directory unless it's been modified


    tree.dirty = false;
    tree.flush(path, ipld, stream.source, function (err, node) {
      if (err) {
        callback(err);
      } else {
        callback(null, node);
      }
    });
  }
}