'use strict';

var pull = require('pull-stream/pull');

var values = require('pull-stream/sources/values');

var asyncMap = require('pull-stream/throughs/async-map');

var collect = require('pull-stream/sinks/collect');

var pushable = require('pull-pushable');

var pullPair = require('pull-pair');

var batch = require('pull-batch');

module.exports = function balancedReduceToRoot(reduce, options) {
  var pair = pullPair();
  var source = pair.source;
  var result = pushable();
  reduceToParents(source, function (err, roots) {
    if (err) {
      result.end(err);
      return; // early
    }

    if (roots.length === 1) {
      result.push(roots[0]);
      result.end();
    } else if (roots.length > 1) {
      result.end(new Error('expected a maximum of 1 roots and got ' + roots.length));
    } else {
      result.end();
    }
  });

  function reduceToParents(_chunks, callback) {
    var chunks = _chunks;

    if (Array.isArray(chunks)) {
      chunks = values(chunks);
    }

    pull(chunks, batch(options.maxChildrenPerNode), asyncMap(reduce), collect(reduced));

    function reduced(err, roots) {
      if (err) {
        callback(err);
      } else if (roots.length > 1) {
        reduceToParents(roots, callback);
      } else {
        callback(null, roots);
      }
    }
  }

  return {
    sink: pair.sink,
    source: result
  };
};