"use strict";

var once = exports.once = function (value) {
  return function (abort, cb) {
    if (abort) return cb(abort);

    if (value != null) {
      var _value = value;
      value = null;
      cb(null, _value);
    } else cb(true);
  };
};

var depthFirst = exports.depthFirst = function (start, createStream) {
  var reads = [],
      ended;
  reads.unshift(once(start));
  return function next(end, cb) {
    if (!reads.length) return cb(true);
    if (ended) return cb(ended);
    reads[0](end, function (end, data) {
      if (end) {
        if (end !== true) {
          ended = end;
          reads.shift();

          while (reads.length) {
            reads.shift()(end, function () {});
          }

          return cb(end);
        } //if this stream has ended, go to the next queue


        reads.shift();
        return next(null, cb);
      }

      reads.unshift(createStream(data));
      cb(end, data);
    });
  };
}; //width first is just like depth first,
//but push each new stream onto the end of the queue


var widthFirst = exports.widthFirst = function (start, createStream) {
  var reads = [];
  reads.push(once(start));
  return function next(end, cb) {
    if (!reads.length) return cb(true);
    reads[0](end, function (end, data) {
      if (end) {
        reads.shift();
        return next(null, cb);
      }

      reads.push(createStream(data));
      cb(end, data);
    });
  };
}; //this came out different to the first (strm)
//attempt at leafFirst, but it's still a valid
//topological sort.


var leafFirst = exports.leafFirst = function (start, createStream) {
  var reads = [];
  var output = [];
  reads.push(once(start));
  return function next(end, cb) {
    reads[0](end, function (end, data) {
      if (end) {
        reads.shift();
        if (!output.length) return cb(true);
        return cb(null, output.shift());
      }

      reads.unshift(createStream(data));
      output.unshift(data);
      next(null, cb);
    });
  };
};