'use strict';

var waterfall = require('async/waterfall');

var DirSharded = require('./dir-sharded');

module.exports = flatToShard;

function flatToShard(child, dir, threshold, options, callback) {
  maybeFlatToShardOne(dir, threshold, options, function (err, newDir) {
    if (err) {
      callback(err);
      return; // early
    }

    var parent = newDir.parent;

    if (parent) {
      waterfall([function (callback) {
        if (newDir !== dir) {
          if (child) {
            child.parent = newDir;
          }

          parent.put(newDir.parentKey, newDir, callback);
        } else {
          callback();
        }
      }, function (callback) {
        if (parent) {
          flatToShard(newDir, parent, threshold, options, callback);
        } else {
          callback(null, newDir);
        }
      }], callback);
    } else {
      // no parent, we're done climbing tree
      callback(null, newDir);
    }
  });
}

function maybeFlatToShardOne(dir, threshold, options, callback) {
  if (dir.flat && dir.directChildrenCount() >= threshold) {
    definitelyShardOne(dir, options, callback);
  } else {
    callback(null, dir);
  }
}

function definitelyShardOne(oldDir, options, callback) {
  var newDir = DirSharded({
    root: oldDir.root,
    dir: true,
    parent: oldDir.parent,
    parentKey: oldDir.parentKey,
    path: oldDir.path,
    dirty: oldDir.dirty,
    flat: false
  }, options);
  oldDir.eachChildSeries(function (key, value, callback) {
    newDir.put(key, value, callback);
  }, function (err) {
    if (err) {
      callback(err);
    } else {
      callback(err, newDir);
    }
  });
}