'use strict';

var pause = require('pull-pause');

var pull = require('pull-stream/pull');

var map = require('pull-stream/throughs/map');

var writable = require('pull-write');

var pushable = require('pull-pushable');

var assert = require('assert');

var setImmediate = require('async/setImmediate');

var DAGBuilder = require('../builder');

var createTreeBuilder = require('./tree-builder');

var chunkers = require('../chunker');

var defaultOptions = {
  chunker: 'fixed',
  rawLeaves: false,
  hashOnly: false,
  cidVersion: 0,
  hash: null,
  leafType: 'file',
  hashAlg: 'sha2-256'
};

module.exports = function (ipld, _options) {
  var options = Object.assign({}, defaultOptions, _options);
  options.cidVersion = options.cidVersion || 0;

  if (options.cidVersion > 0 && _options.rawLeaves === undefined) {
    // if the cid version is 1 or above, use raw leaves as this is
    // what go does.
    options.rawLeaves = true;
  }

  if (_options && _options.hash !== undefined && _options.rawLeaves === undefined) {
    // if a non-default hash alg has been specified, use raw leaves as this is
    // what go does.
    options.rawLeaves = true;
  }

  var Chunker = chunkers[options.chunker];
  assert(Chunker, 'Unknkown chunker named ' + options.chunker);
  var pending = 0;
  var waitingPending = [];
  var entry = {
    sink: writable(function (nodes, callback) {
      pending += nodes.length;
      nodes.forEach(function (node) {
        return entry.source.push(node);
      });
      setImmediate(callback);
    }, null, 1, function (err) {
      return entry.source.end(err);
    }),
    source: pushable()
  };
  var dagStream = DAGBuilder(Chunker, ipld, options);
  var treeBuilder = createTreeBuilder(ipld, options);
  var treeBuilderStream = treeBuilder.stream();
  var pausable = pause(function () {}); // TODO: transform this entry -> pausable -> <custom async transform> -> exit
  // into a generic NPM package named something like pull-pause-and-drain

  pull(entry, pausable, dagStream, map(function (node) {
    pending--;

    if (!pending) {
      process.nextTick(function () {
        while (waitingPending.length) {
          waitingPending.shift()();
        }
      });
    }

    return node;
  }), treeBuilderStream);
  return {
    sink: entry.sink,
    source: treeBuilderStream.source,
    flush: flush
  };

  function flush(callback) {
    pausable.pause(); // wait until all the files entered were
    // transformed into DAG nodes

    if (!pending) {
      proceed();
    } else {
      waitingPending.push(proceed);
    }

    function proceed() {
      treeBuilder.flush(function (err, hash) {
        if (err) {
          treeBuilderStream.source.end(err);
          callback(err);
          return;
        }

        pausable.resume();
        callback(null, hash);
      });
    }
  }
};