'use strict';

var CID = require('cids');

var pull = require('pull-stream/pull');

var values = require('pull-stream/sources/values');

var error = require('pull-stream/sources/error');

module.exports = function (cid, node, name, path, pathRest, resolve, size, dag, parent, depth) {
  var newNode;

  if (pathRest.length) {
    var pathElem = pathRest[0];
    newNode = node[pathElem];
    var newName = path + '/' + pathElem;

    if (!newNode) {
      return error(new Error("not found"));
    }

    var isCID = CID.isCID(newNode);
    return pull(values([{
      depth: depth,
      name: pathElem,
      path: newName,
      pathRest: pathRest.slice(1),
      multihash: isCID && newNode,
      object: !isCID && newNode,
      parent: parent
    }]), resolve);
  } else {
    return error(new Error('invalid node type'));
  }
};