'use strict';

var through = require('pull-through');

var DEFAULT_MAX_LENGTH = 100;

module.exports = function block(_maxLength) {
  var maxLength = _maxLength || DEFAULT_MAX_LENGTH;
  var buffered = [];
  return through(function transform(data) {
    buffered = buffered.concat(data);

    while (buffered.length >= maxLength) {
      var end = maxLength;
      var slice = buffered.slice(0, end);
      buffered = buffered.slice(end);
      this.queue(slice);
    }
  }, function flush(end) {
    if (buffered.length) {
      this.queue(buffered);
      buffered = [];
    }

    this.queue(null);
  });
};