'use strict';

var error = require('pull-stream/sources/error');

var once = require('pull-stream/sources/once');

var empty = require('pull-stream/sources/empty');

var extractDataFromBlock = require('./extract-data-from-block'); // Logic to export a single raw block


module.exports = function (cid, node, name, path, pathRest, resolve, size, dag, parent, depth, options) {
  var accepts = pathRest[0];

  if (accepts !== undefined && accepts !== path) {
    return empty();
  }

  size = size || node.length;
  var offset = options.offset;
  var length = options.length;

  if (offset < 0) {
    return error(new Error('Offset must be greater than or equal to 0'));
  }

  if (offset > size) {
    return error(new Error('Offset must be less than the file size'));
  }

  if (length < 0) {
    return error(new Error('Length must be greater than or equal to 0'));
  }

  if (length === 0) {
    return once({
      depth: depth,
      content: once(Buffer.alloc(0)),
      hash: cid,
      name: name,
      path: path,
      size: size,
      type: 'raw'
    });
  }

  if (!offset) {
    offset = 0;
  }

  if (!length || offset + length > size) {
    length = size - offset;
  }

  return once({
    depth: depth,
    content: once(extractDataFromBlock(node, 0, offset, offset + length)),
    hash: cid,
    name: name,
    path: path,
    size: size,
    type: 'raw'
  });
};