'use strict';

var waterfall = require('async/waterfall');

var dagPB = require('ipld-dag-pb');

var UnixFS = require('ipfs-unixfs');

var persist = require('../utils/persist');

var DAGLink = dagPB.DAGLink;
var DAGNode = dagPB.DAGNode;

module.exports = function reduce(file, ipld, options) {
  return function (leaves, callback) {
    if (leaves.length === 1 && leaves[0].single && options.reduceSingleLeafToSelf) {
      var leaf = leaves[0];
      return callback(null, {
        size: leaf.size,
        leafSize: leaf.leafSize,
        multihash: leaf.multihash,
        path: file.path,
        name: leaf.name
      });
    } // create a parent node and add all the leaves


    var f = new UnixFS('file');
    var links = leaves.map(function (leaf) {
      f.addBlockSize(leaf.leafSize);
      return new DAGLink(leaf.name, leaf.size, leaf.multihash);
    });
    waterfall([function (cb) {
      return DAGNode.create(f.marshal(), links, cb);
    }, function (node, cb) {
      return persist(node, ipld, options, cb);
    }], function (error, result) {
      if (error) {
        return callback(error);
      }

      callback(null, {
        size: result.node.size,
        leafSize: f.fileSize(),
        multihash: result.cid.buffer,
        path: file.path,
        name: ''
      });
    });
  };
};